<template>
  <!--<div v-theme:column = "'narrow'" id="show-blog">-->
  <div v-theme = "'narrow'" id="show-blog">
  	<h2>博客总览</h2>
  	<input type="text" v-model="search" placeholder="搜素"/>
  	<div v-for="blog in filteredBlogs" class="single-blog">
  		<router-link :to="'/blog/' + blog.id">
  			<h2 v-rainbow>{{ blog.title | to-uppercase}}</h2>
  		</router-link>
  		<article>
  			{{ blog.content | snippet}}
  		</article>
  	</div>
  </div>
</template>

<script>
export default {
  name: 'show-blog',
  data(){
  	return{
  		blogs:[],
  		search:""
  	}
  },
  created(){
  	this.$http.get("https://blog-46a55.firebaseio.com/posts.json").then(function(data){
		console.log(data);
		console.log(data.json());
		return data.json()
//		this.blogs = data.body.slice(0,10);
  	}).then(function(data){
  		console.log(data);
  		var blogArray = [];
  		for(let key in data){
  			console.log(key);
  			console.log(data[key]);
  			data[key].id = key;
  			blogArray.push(data[key]);
  		}
  		console.log(blogArray);
  		this.blogs = blogArray;
  	})
  	//本地请求文件只能放static文件中
  	/*this.$http.get("./../static/posts.json").then(function(data){
//		console.log(data);
  		this.blogs = data.body.slice(0,10);
  		console.log(this.blogs);
  	})*/
  },
  computed:{
  	filteredBlogs:function(){
  		return this.blogs.filter((blog) =>{
  			 return blog.title.match(this.search);
  		})
  	}
  },
  //局部过滤
  filters:{
  	/*"to-uppercase":function(value){
  		return value.toUpperCase()
  	}*/
  	toUppercase(value){
  		return value.toUpperCase()
  	}
  },
  //局部指令
  directives:{
  	'rainbow':{
  		bind(el,binding,vnode){
			el.style.color = "#" + Math.random().toString(16).slice(2,8)
		}
  	}
  }
}
</script>

<style>
	#show-blog{
		max-width:800px;
		margin: 0 auto;
	}
	.single-blog{
		padding: 20px;
		margin: 20px 0;
		box-sizing: border-box;
		background: #eee;
		border: 1px dotted #aaa;
	}
	#show-blog a{
		color: #444;
		text-decoration: none;
		cursor: pointer;
	}
	input[type="text"]{
		padding: 8px;
		width: 100%;
		box-sizing: border-box;
	}
</style>



